<template>
  <div id="app">
    <div class="app-phone">

      <div class="phone-header">
        <a href="javaScript:;" class="cancel-cta" v-if="step==2||step==3" @click="backLast">Cancal</a>
        <img src="./assets\vue_gram_logo_cp.png" alt="">
        <a href="javaScript:;" class="next-cta" v-if="step==2" @click="step++">Next</a>
        <a href="javaScript:;" class="next-cta" v-if="step==3" @click="share">Share</a>
      </div>

      <phone-body :posts="posts" :step="step" :myimage="image" :filters="filters" :selected="selected" v-model="caption"></phone-body>
      
      <div class="phone-footer">
        <div class="home-cta" @click="this.step=1">
          <i class="fas fa-home fa-lg"></i>
        </div>
        <div class="upload-cta">
          <input type="file" id="file" name="file" @change="uploadImage" :disabled="this.step !== 1">
          <label for="file">
            <i class="far fa-plus-square fa-lg"></i>
          </label>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import phoneBodyVue from './components/phoneBody.vue';
import posts from "./data/posts";
import filters from "./data/filters.js";
import eventBus from "./eventBus.js";

export default {
  name: 'app',
  created(){
    eventBus.$on("selectFilter",val=>{
      this.selected = val
    })
  },
  data(){
    return {
      posts,
      filters,
      step:1,
      image:"",
      selected:"",
      caption:"666"
    }
  },
  components:{
    phoneBody:phoneBodyVue
  },
  methods:{
    uploadImage(event){
      const files = event.target.files;
      if(!files.length){ return null;}
      const reader = new FileReader();
      reader.readAsDataURL(files[0]);
      reader.onload = val =>{
        const image = val.target.result;
        this.step = 2;
        this.image = image;
      }
    },
    backLast(){
      if(this.step == 2){
        this.image = "";
        this.selected = "";
        this.step = 1;}
      else{this.step = 2}
    },
    share(){
      const post= {
      username: "liuluwei",
      userImage: "https://s3-us-west-2.amazonaws.com/s.cdpn.io/1211695/me_3.jpg",
      postImage:
        this.image,
      likes: 0,
      hasBeenLiked: false,
      caption: this.caption,
      filter: this.selected
      };
      this.posts.unshift(post);
      this.step = 1;
    }
  }
}
</script>

<style lang="scss" src="./styles/app.scss">
  
</style>


