<template>
    <div class="filter-type">
        <p>{{filter.name}}</p>
        <div 
            :style="{backgroundImage:'url('+myimage+')'}"
            :class="filter.name"
            @click="selectFilter"
            class="img">
        </div>
    </div>
</template>

<script>
import eventBus from "../eventBus";

export default {
    props:{
        filter:Object,
        myimage:String
    },
    methods:{
        selectFilter(){
            eventBus.$emit("selectFilter",this.filter.name)
        }
    }
}
</script>

