<template>
    <div class="phone-body">
        <transition name="myfade" mode="out-in" appear>
            <div class="feed" v-if="step == 1" v-dragscroll key="step1">
                <vuegram-post
                    v-for="(post,index) in posts" :key="index"
                    :post="post"
                    :step="step"
                ></vuegram-post>
            </div>
        
            <div v-else-if="step == 2" key="step2">
                <div class="selected-image" :class="selected" :style="{backgroundImage:'url('+myimage+')'}"></div>
                <div class="filter-container" v-dragscroll>
                    <filters-vue 
                        v-for="(filter,index) in filters" 
                        :key="index" 
                        :filter="filter" 
                        @selectFilter="selectFilter"
                        :myimage="myimage">
                    </filters-vue>
                </div>
            </div>
       
            <div v-else key="step3">
                <div class="selected-image" :class="selected" :style="{backgroundImage:'url('+myimage+')'}"></div>
                <div class="caption-container">
                    <textarea placeholder="very Good!" :value="value" @input="inputText" key="step3333"></textarea>
                </div>
            </div>

        </transition>
    </div>
</template>

<script>
import vuegramPostVue from './vuegramPost.vue';
import filterVue from './filter.vue';

export default {
    props:{
        posts:Array,
        step:Number,
        myimage:String,
        filters:Array,
        selected:String,
        value:String
    },
    components:{
        vuegramPost:vuegramPostVue,
        filtersVue:filterVue
    },
    methods:{
        selectFilter(val){
            this.selected = val
        },
        inputText(val){
            this.$emit("input",val.target.value)
        }
    }
}
</script>

<style lang="scss" src="../styles/phone-body.scss">

</style>
<style lang="scss" src="../styles/vuegram-post.scss">

</style>
<style lang="scss" src="../styles/filter-type.scss">

</style>
<style>
    .myfade-enter-active,.myfade-leave-active {
        transition: opacity 0.5s;
    }
    .myfade-enter,.myfade-leave-to{
        opacity: 0;
        z-index: 10;
    }
</style>



